//
// Font Awesome 5.x Overrides
// --------------------------------------------------

// Variables Override
$fa-font-path: "../../fonts/fontawesome5-pro" !default;

// Import Font Awesome 5 Pro
@import '~@fortawesome/fontawesome-pro/scss/fontawesome.scss';
//@import './fontawesome5-pro/fontawesome.scss';
// @import '~@fortawesome/fontawesome-pro/scss/light.scss';
@import '~@fortawesome/fontawesome-pro/scss/regular.scss';
//@import './fontawesome5-pro/regular.scss';
// @import '~@fortawesome/fontawesome-pro/scss/solid.scss';
// @import '~@fortawesome/fontawesome-pro/scss/duotone.scss';
// @import '~@fortawesome/fontawesome-pro/scss/brands.scss';

// Apply small fixes to reflect the new changes in Font Awesome 5 version
.block.block-mode-loading.block-mode-loading-refresh::after,
.block.block-mode-loading.block-mode-loading-sun::after,
.block.block-mode-loading.block-mode-loading-repeat::after,
.breadcrumb-item + .breadcrumb-item::before,
.css-checkbox .css-control-input ~ .css-control-indicator::after,
.list-events > li::before,
.nav-main-header a.nav-submenu::before,
.nav-main a.nav-submenu::before,
.nav-main a.nav-submenu::after {
    font-family: 'Font Awesome 5 Pro';
}

.block.block-mode-loading.block-mode-loading-refresh::after,
.block.block-mode-loading.block-mode-loading-repeat::after,
.breadcrumb-item + .breadcrumb-item::before,
.css-checkbox .css-control-input ~ .css-control-indicator::after {
    font-weight: 900;
}
